<template>
	<div class="home_view">
		<div class="projectTitle">欢迎使用 {{projectName}}</div>
		<div class="count_list">
			<el-collapse-transition v-if="btnAuth('shoucheng','首页总数')">
				<el-card v-show="countTypeList.closeshouchengCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								收成
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenshouchengCountType')" class="showIcons"
									:class="countTypeList.hiddenshouchengCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closeshouchengCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenshouchengCountType">
							<div class="count_title">收成总数</div>
							<div class="count_num">{{shouchengCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('nongzuowuxiaoshou','首页总数')">
				<el-card v-show="countTypeList.closenongzuowuxiaoshouCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								农作物销售
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddennongzuowuxiaoshouCountType')" class="showIcons"
									:class="countTypeList.hiddennongzuowuxiaoshouCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closenongzuowuxiaoshouCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddennongzuowuxiaoshouCountType">
							<div class="count_title">农作物销售总数</div>
							<div class="count_num">{{nongzuowuxiaoshouCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
		<div class="card_list">
			<el-collapse-transition v-if="btnAuth('shoucheng','首页统计')">
				<el-card v-show="cardTypeList.closeshouchengChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								收成
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenshouchengChartType1')" class="showIcons"
									:class="cardTypeList.hiddenshouchengChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closeshouchengChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenshouchengChartType1">
							<div id="shouchengchanliangEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('nongzuowuxiaoshou','首页统计')">
				<el-card v-show="cardTypeList.closenongzuowuxiaoshouChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								农作物销售
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddennongzuowuxiaoshouChartType1')" class="showIcons"
									:class="cardTypeList.hiddennongzuowuxiaoshouChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closenongzuowuxiaoshouChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddennongzuowuxiaoshouChartType1">
							<div id="nongzuowuxiaoshouxiaoshoujineEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
	</div>
</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closeshouchengCountType = true
		countTypeList.value.hiddenshouchengCountType = true
		if(btnAuth('shoucheng','首页总数')){
			getshouchengCount()
		}
		countTypeList.value.closenongzuowuxiaoshouCountType = true
		countTypeList.value.hiddennongzuowuxiaoshouCountType = true
		if(btnAuth('nongzuowuxiaoshou','首页总数')){
			getnongzuowuxiaoshouCount()
		}
	}
	const shouchengCount = ref(0)
	const getshouchengCount = () => {
		context?.$http({
			url:'shoucheng/count',
			method: 'get'
		}).then(res=>{
			shouchengCount.value = res.data.data
		})
	}
	const nongzuowuxiaoshouCount = ref(0)
	const getnongzuowuxiaoshouCount = () => {
		context?.$http({
			url:'nongzuowuxiaoshou/count',
			method: 'get'
		}).then(res=>{
			nongzuowuxiaoshouCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closeshouchengChartType1: true,
		hiddenshouchengChartType1: true,
		closenongzuowuxiaoshouChartType1: true,
		hiddennongzuowuxiaoshouChartType1: true,
	})
	const getCardList = () => {
		if(btnAuth('shoucheng','首页统计')){
			getshouchengChart1()
		}
		if(btnAuth('nongzuowuxiaoshou','首页统计')){
			getnongzuowuxiaoshouChart1()
		}
	}
	const getshouchengChart1 = () => {
		nextTick(()=>{
			var chanliangEchart1 = echarts.init(document.getElementById("shouchengchanliangEchart1"),'macarons');
			context?.$http({
				url: `shoucheng/value/zuowumingcheng/chanliang`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].zuowumingcheng);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].zuowumingcheng
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '收成统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				chanliangEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				chanliangEchart1.setOption(option);
				//根据窗口的大小变动图表
				chanliangEchart1.resize();
			})
		})
	}
	const getnongzuowuxiaoshouChart1 = () => {
		nextTick(()=>{
			var xiaoshoujineEchart1 = echarts.init(document.getElementById("nongzuowuxiaoshouxiaoshoujineEchart1"),'macarons');
			context?.$http({
				url: `nongzuowuxiaoshou/value/zuowumingcheng/xiaoshoujine`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].zuowumingcheng);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].zuowumingcheng
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '销售金额统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				xiaoshoujineEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				xiaoshoujineEchart1.setOption(option);
				//根据窗口的大小变动图表
				xiaoshoujineEchart1.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 20px 0;
		margin: 20px 0 20px;
		color: #4F1904;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 22px;
		justify-content: center;
		align-items: center;
		height: auto;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		grid-column-gap: 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 1px solid #ddd;
			box-shadow: 0px 0px 0px rgba(0,0,0,.12);
			padding-top: 60px;
			flex: 1;
			background: linear-gradient(90deg,#FFFAF2, #FDE9CB);
			width: 100%;
			position: relative;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border-radius: 20px 0 0 20px;
				padding: 0 20px;
				top: 20px;
				background: #EED6B4;
				line-height: 40px;
				position: absolute;
				right: 0;
				border-bottom: none;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #313131;
						font-size: 14px;
						margin-right: 40px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #313131;
							font-weight: 700;
							font-size: 14px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.count_item{
					padding: 30px;
					text-align: center;
					// 总数标题
					.count_title{
						color: #9E9E9E;
						font-size: 18px;
						line-height: 2;
						text-align: left;
					}
					// 总数数字
					.count_num{
						color: #E7B668;
						font-weight: 700;
						font-size: 64px;
						line-height: 1;
						text-align: left;
					}
				}
			}
		}
	}
	// 首页盒子
	.home_view {
		padding: 0px 30px;
		margin: 0;
		background: url(http://clfile.zggen.cn/20240203/480e76b8afa24188a2f7a3a171f56e7d.png) no-repeat center top / 100% 100% !important;
		width: 100%;
		min-height: 100vh;
		height: auto;
	}
	// 统计图盒子
	.card_list {
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap;
		// 统计图card
		.card_view {
			border: 1px solid #ddd;
			box-shadow: 0px 0px 0px rgba(0,0,0,.12);
			padding-top: 60px;
			margin: 0 1% 30px;
			width: 48%;
			position: relative;
			box-sizing: border-box;
			height: auto;
			// 头部
			.el-card__header {
				border-radius: 20px 0px 0px 20px;
				padding: 0px 20px;
				top: 20px;
				background: rgb(238, 214, 180);
				line-height: 40px;
				position: absolute;
				right: 0;
				border-bottom: none;
				// 头部盒子
				.index_card_head {
					display: flex;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #313131;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon{
							cursor: pointer;
							color: #313131;
							font-weight: 700;
							font-size: 14px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.card_item{
					padding: 30px;
					text-align: center;
				}
			}
		}
	}
</style>
